import React from "react"
import ReactMarkdown from "react-markdown"
import { createChunkMap } from "@rp/react/common/hooks"
import { remarkGfmNoAutoLink } from "@rp/web/utils"
import { ChatResponseText } from "../../Chat.styles"
import { OnUserInteractionProps } from "../ChatResponse"
import { inlineAttributionRenderer } from "../InlineAttribution/inlineAttributionRenderer"

type ChunkMap = ReturnType<typeof createChunkMap>["chunkMap"]
type ExternalMap = ReturnType<typeof createChunkMap>["externalMap"]

type Props = {
  contents: string
  isReady: boolean
  inlineAttributionProps: {
    chunkMap: ChunkMap
    externalMap: ExternalMap
    onUserInteraction: (props: OnUserInteractionProps) => void
  }
}

const TextBlock = ({ contents, isReady, inlineAttributionProps }: Props) => {
  return (
    <ChatResponseText>
      <ReactMarkdown
        remarkPlugins={[remarkGfmNoAutoLink]}
        components={{
          code: inlineAttributionRenderer({
            chunkMap: inlineAttributionProps.chunkMap,
            externalMap: inlineAttributionProps.externalMap,
            disabled: !isReady,
            onUserInteraction: inlineAttributionProps.onUserInteraction,
          }),
        }}
      >
        {contents}
      </ReactMarkdown>
    </ChatResponseText>
  )
}

export { TextBlock }
